@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@0,900;1,100;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cursorNone {
  cursor: none;
}

.page{
  background-color: 		rgb(48,48,48);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height:100vh;
  width:100vw;
  overflow: auto;
}

.mainProgress{
  margin: auto;
  width: 200px;
}

.subProgress{
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

.subProgressComponent{
  width: 230px;
  height: auto;
  padding-top: 0px;
}

.percentage{
  text-align: center;
  color: aqua;
  font-style: italic;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.subProgressTitle{
  text-align: center;
  color: #CC9933;
  font-family: 'Piazzolla', serif;
  font-weight: 100;
  font-style: italic;
}

.mainProgressHead{
  font-family: 'Piazzolla', serif;
  color: #99FF66;
  text-align: center;
}

.mainProgressDaysLeft{
  font-family: 'Piazzolla', serif;
  font-weight: 400;
  font-style: italic;
  color: white;
  text-align: center;
}

.subProgressDaysLeft{
  font-family: 'Piazzolla', serif;
  font-weight: 400;
  font-style: italic;
  text-align: center;
  color: #CCFFFF;
}

.mainProgressPercentage{
  font-family: 'Piazzolla', serif;
  font-weight: 400;
  font-style: italic;
  font-style: italic;
  color: #CCFFFF;
  text-align: center;
}
